/*///////////// _CARDS.scss start /////////////*/

:root {
    --card-background-color: var(--slate-900);
    --card-border-color: var(--accent-color-30pct);
}

.card {
    background: var(--card-background-color);
    border: 1px solid var(--card-border-color);
    border-radius: 12px;
    padding-inline: 24px;
    padding-block: 24px 32px;
    display: flex;
    justify-content: center;
}

.card p {
    margin-bottom: 0;
}

/*///////////// _CARDS.scss end /////////////*/
