/* Scroll General-Values */
/* -+-+-+-+-+-+-+-+-+-+-+*/
@keyframes scroll {
  to {
    transform: translate(calc(-50% - var(--gap, 30px) / 2)); /* la mitad del gap*/
  }
}
.scroller {
  margin-inline: auto;
}

.scroller__inner {
  padding-block: 1rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: var(--gap, 30px);
}

.scroller[data-animated=true] {
  overflow: hidden;
  -webkit-mask: linear-gradient(90deg, transparent, white 10%, white 90%, transparent);
  mask: linear-gradient(90deg, transparent, white 10%, white 90%, transparent);
}

@media (max-width: 1100px) {
  .scroller[data-animated=true] {
    -webkit-mask: unset;
    mask: unset;
  }
  section:has(> .scroller) {
    width: 100%;
  }
}
.scroller[data-animated=true] .scroller__inner {
  width: max-content;
  flex-wrap: nowrap;
  animation: scroll var(--_animation-duration, 65s) var(--_animation-direction, forwards) linear infinite;
}

.scroller[data-direction=right] {
  --_animation-direction: reverse;
}

.scroller[data-direction=left] {
  --_animation-direction: forwards;
}

.scroller[data-duration=slow] {
  --_animation-duration: 60s;
}

.scroller[data-duration=fast] {
  --_animation-duration: 20s;
}

.scroller[data-duration=custom] {
  --_animation-duration: var(--duration);
}

